a {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
  color: white;
}

a:hover {
  text-decoration: none;
  color: white;
}

a:focus {
  text-decoration: none;
  color: white;
}

a:hover, a:active {
  text-decoration: none;
  color: white;
}

body {
  background-color: #37414e;
  overflow: auto;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95vW;
  height: auto;
  background-color: #37414e;
}

.header-profile-image {
  height: 9vH;
  padding-top: 4.5vH;
  padding-left: 4.5vH;
  justify-self: first baseline;
}

.header-home-container {
  width: 11vW;
  height: 9vH;
  background-color: #FECD41;
  border-radius: 4.5vH;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 4.5vH;
  margin-top: 4.5vH;
}
.header-home-container:hover {
  filter: brightness(0.85);
  cursor: pointer;
}

.header-home-icon {
  height: 4.75vH;
}

.header-home-label {
  font-family: "JetBrains Mono", monospace;
  font-weight: 400;
  color: black;
  font-size: 1.5em;
  padding-left: 0.3em;
}

.content-temp {
  padding-top: 5rem;
  display: flex;
  justify-content: center;
  height: 8.5em;
}

.content-temp-data {
  font-family: "JetBrains Mono", monospace;
  font-weight: 900;
  color: #FECD41;
  font-size: 8.5em;
}

.content-temp-unit {
  font-family: "JetBrains Mono", monospace;
  font-weight: 900;
  color: #FECD41;
  font-size: 2em;
  padding-bottom: 4rem;
}

.location-container {
  width: 95vW;
  height: 2em;
  display: flex;
  justify-content: center;
}

.locaction-data {
  font-family: "Reddit Sans", sans-serif;
  font-weight: 300;
  color: rgb(230, 230, 230);
  font-size: 2em;
}

.current-label {
  padding-top: 5rem;
  font-family: "Reddit Sans", sans-serif;
  font-weight: 700;
  color: rgb(230, 230, 230);
  font-size: 2em;
}

.current-container {
  width: 95vW;
  height: 2em;
  display: flex;
  justify-content: center;
}

.multi-info-container {
  width: 95vW;
  display: flex;
  justify-content: center;
  padding-top: 10rem;
}

.multi-data-container {
  width: 30vW;
  height: 15vH;
  border-color: #FECD41;
  border-style: solid;
  border-width: 0.25rem;
  border-radius: 1.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-inline: 2rem;
  margin-bottom: 1rem;
}

.multi-data-label {
  font-family: "Reddit Sans", sans-serif;
  font-weight: 400;
  color: #FECD41;
  font-size: 2em;
  padding-inline: 3rem;
}

.multi-data-sperator {
  width: 0.15rem;
  height: 8vH;
  background-color: #FECD41;
}

