// FUNCTIONS AND MIXINS
@mixin userSelect{
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;}
@mixin font($type, $weight:400, $color:black, $size:1em) {
    $fontSerif: "New York", serif;
    $fontSans: "Reddit Sans", sans-serif;
    $fontMono: "JetBrains Mono", monospace;
    @if $type == 'serif' {
        font-family: $fontSerif;
    } @else if $type == 'sans' {
        font-family: $fontSans;
    } @else if $type == 'mono' {
        font-family: $fontMono;
    }
    font-weight: $weight;
    color: $color;
    font-size: $size;}
@mixin selectable {
    &:hover {
        filter: brightness(0.85);
        cursor: pointer;
    }}
@mixin highlight {
    ::selection {
        background: #425C57; /* WebKit/Blink Browsers */
      }
      ::-moz-selection {
        background: #425C57; /* Gecko Browsers */
      }}

$backingColor: #37414e;
$fontColor: rgb(230, 230, 230);
$accentColor: #FECD41;

// CLASSES FOR HOMEPAGE
a { text-decoration: none; }
a:visited { text-decoration: none; color:white; }
a:hover { text-decoration: none; color:white; }
a:focus { text-decoration: none; color:white; }
a:hover, a:active { text-decoration: none; color:white }

body{
    background-color: $backingColor;
    overflow:auto;
    @include userSelect()
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95vW;
    height: auto;
    background-color: $backingColor;
  }

.header-profile-image{
    height: 9vH;
    padding-top: 4.5vH;
    padding-left: 4.5vH;
    justify-self: first baseline;
}

.header-home-container{
    width: 11vW;
    height: 9vH;
    background-color: $accentColor;
    border-radius: 4.5vH;
    display: flex;
    align-items: center;
    justify-content: center;
    @include selectable();
    margin-right: 4.5vH;
    margin-top: 4.5vH;
}

.header-home-icon{
    height: 4.75vH;
}

.header-home-label{
    @include font('mono', 400, black, 1.5em);
    padding-left: 0.3em;
}

.content-temp{
    padding-top: 5rem;
    display: flex;
    justify-content: center;
    height: 8.5em;
}

.content-temp-data{
    @include font('mono', 900, $accentColor, 8.5em)
}

.content-temp-unit{
    @include font('mono', 900, $accentColor, 2em);
    padding-bottom: 4rem;
}

.location-container{
    width: 95vW;
    height: 2em;
    display: flex;
    justify-content: center
}

.locaction-data{
    @include font('sans', 300, $fontColor, 2em);
}

.current-label{
    padding-top: 5rem;
    @include font('sans', 700, $fontColor, 2em);
}

.current-container{
    width: 95vW;
    height: 2em;
    display: flex;
    justify-content: center
}

.multi-info-container{
    width: 95vW;
    display: flex;
    justify-content: center;
    padding-top: 10rem;
}

.multi-data-container{
    width: 30vW;
    height: 15vH;
    border-color: $accentColor;
    border-style: solid;
    border-width: 0.25rem;
    border-radius: 1.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-inline: 2rem;
    margin-bottom: 1rem;
}

.multi-data-label{
    @include font('sans', 400, $accentColor, 2em);
    padding-inline: 3rem;
}

.multi-data-sperator{
    width: 0.15rem;
    height: 8vH;
    background-color: #FECD41;
}
